/* Container */
.comingsoon-container {
  background-color: #000;
  color: #fff;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 32px;

}

.logo {
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 1px;
}

.logo-highlight {
  color: #b3b3b3;
}

.logo-img {
  height: 50px;
  width: 100px;
  object-fit: contain;
}

.menu-btn {
  background: none;
  border: none;
  color: #fff;
  font-size: 15px;
  letter-spacing: 0.5px;
  cursor: pointer;
}

.menu-icon {
  margin-left: 5px;
  font-size: 18px;
}

/* Main Content */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.rocket-img {
  width: 110px;
  margin-bottom: 14px;
}

.main-title {
  font-size: 32px;
  font-weight: bold;
  letter-spacing: 1px;
  margin-bottom: 1px;
}

.subtitle {
  color: white;
  font-size: 16px;
  margin-top: 0;
}
.footer {
  padding: 20px 130px;
  font-size: 14px;
  color: #2d2c2c;
}

/* ===== Row 1: Links + Icons ===== */
.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  border-bottom: 1px solid rgb(43, 42, 42);
}

.footer-links {
  display: flex;
  gap: 20px;
}

.footer-links a {
  color: rgb(196, 190, 190);
  text-decoration: none;
}
.footer-links a:hover { color: #fff; }

.footer-social .social-icons {
  display: flex;
  gap: 12px;
}

/* ===== Row 2: Small Links + Copy ===== */
.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 5px;
}

.footer-small-links .small-links {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.footer-small-links a {
  color: rgb(196, 190, 190);
  text-decoration: none;
}
.footer-small-links a:hover { color: #fff; }

.footer-copy {
  font-size: 13px;
  color: rgb(196, 190, 190);
}



/* Social Icons */
.social-icons {
  display: flex;
  gap: 10px;
  align-items: center;

}

.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  background: none;
  color: white;
  border: none;
  padding: 0;
  cursor: pointer;
}

.icon:hover {
  color: #ccc;
}


.facebook-icon {
  background-color: #fff;
  /* White background */
  color: #000;
  /* Black "f" */
  border: 1px solid #000;
  border-radius: 50%;
  /* Circle */
  width: 17px;
  height: 17px;

  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  /* Icon size */
  transition: all 0.3s ease;
}

.linkedin-icon {
  background-color: #fff;
  /* White background */
  color: #000;
  /* Black "in" */
  border: 1px solid #000;
  border-radius: 4px;
  /* Curved edges */
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  /* Icon size */
  transition: all 0.3s ease;
}


/* Menu Overlay (White Bar) */
.menu-overlay {
  position: fixed;
  top: 3%;
  left: 5%;
  width: 86%;
  height: 40px;
  background: #fff;
  color: #000;
  z-index: 2000;
  display: flex;
  align-items: center;
  padding: 12px 32px;
  border-radius: 6px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}


.menu-links {
  display: flex;
  gap: 28px;
  flex: 1;
  justify-content: center;
  margin-right: 50%;
}

.menu-links a {
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  color: #000;
}

.menu-links a:hover {
  color: #444;
}

/* Right Close Button */
.menu-close-btn {
  position: absolute;
  top: 14px;
  /* distance from top */
  right: 20px;
  /* distance from right */
  background: none;
  color: #000;
  /* black cross */
  font-size: 24px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  z-index: 5000;
}


.menu-logo img {
  height: 50px;
  width: 100px;
  object-fit: contain;
}
/* Mobile Responsive Fix */
@media (max-width: 768px) {

  /* Remove header line */
  .header {
    border-bottom: none;
  }

  /* Footer adjustments */
  .footer {
    border-top: none;
    padding: 20px;
    text-align: left;
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  /* Flatten footer rows so we can reorder children */
  .footer-top,
  .footer-bottom {
    display: contents; /* keeps children but removes wrapper for layout */
  }

  /* Top links (Home, Story, Career...) */
  .footer-links {
    order: 1;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    gap: 0;
    margin-bottom: 15px;
  }

  .footer-links a {
    display: block;
    width: 100%;
    padding: 10px 0;
    border-bottom: 1px solid #444;
    text-align: left;
  }

  /* Remove last border line (Patent & Trademark) */
  .footer-links a:last-child {
    border-bottom: none;
  }

  /* Middle row (Privacy, Terms, Refund, etc.) */
  .footer-small-links {
    order: 2;
    width: 100%;
  }

  .footer-small-links .small-links {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    justify-content: flex-start;
    margin: 5px 0;
  }

  .small-links span {
    display: none; /* remove dots */
  }

  /* Social icons */
  .footer-social {
    order: 3;
    width: 100%;
  }

  .footer-social .social-icons {
    display: flex;
    justify-content: flex-start;
    gap: 10px;
    margin: 5px 0;
  }

  /* Copyright text */
  .footer-copy {
    order: 4;
    font-size: 12px;
    text-align: left;
    margin-top: 5px;
  }

  /* Main text sizes */
  .main-title {
    font-size: 24px;
  }

  .subtitle {
    font-size: 14px;
  }

  /* Mobile Menu */
  .menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; /* full height */
    background: #fff;
    color: #000;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px 20px 250px 20px; 
    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden; /* prevent horizontal scroll */
  }

  .menu-logo img {
    height: 50px;
    width: 120px;
    object-fit: contain;
    margin-bottom: 10px;
  }

  .menu-close-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    background: none;
    color: #000;
    font-size: 28px;
    font-weight: bold;
    border: none;
    cursor: pointer;
  }

  .menu-links {
    display: flex;
    flex-direction: column;
    gap: 25px;
    width: 100%;
  }

  .menu-links a {
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;
    color: #000;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
  }

  .menu-links a:last-child {
    border-bottom: none;
  }

  .menu-links a:hover {
    color: #444;
  }
}
